.wp-block-media-text.is-image-fill {
  // the container with both image and text
  border-radius: 1.5rem 1.5rem 1rem 1rem;
  @media screen and (min-width: 599px) {
    border-radius: 1.5rem 1rem 1rem 1.5rem;
  }
  .wp-block-button__link,
  .is-style-popup-menu summary {
    @media screen and (max-width: 1099px) {
      font-size: 0.92rem;
    }
  }
  @media screen and (max-width: 1100px) {
    --wp--preset--font-size--medium: 1rem;
  }
  --image-height: 192px;
  position: relative;
  width: 100%;
  grid-template-rows: var(--image-height) 1fr;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  // border-radius: 1rem;
  // overflow: hidden;
  @media screen and (min-width: 599px) {
    --image-height: 240px;
    display: grid;
  }
  .wp-block-media-text__media {
    // the image on the left
    width: 100%;
    height: 100%;
    max-height: var(--image-height);
    min-height: var(--image-height);
    height: var(--image-height);
    box-sizing: border-box;
    overflow: hidden !important;
    border-radius: 1rem 1rem 0 0;

    @media screen and (min-width: 599px) {
      border-radius: 1rem 0 0 1rem;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      position: absolute;
      width: 40%;
    }
    img {
      height: auto;
      overflow: visible;
      width: 100%;
    }
  }
  .wp-block-media-text__content {
    // the text on the right
    box-sizing: border-box;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    padding: 0.75rem 1.125rem !important;
    min-height: var(--image-height);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    width: 100%;
    @media screen and (min-width: 600px) {
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;
    }
    @media screen and (min-width: 1100px) {
      padding: 0.75rem 1.5rem !important;
    }
  }
}

// IN-PAGE POPUP MENU

#howcanwehelp-2 {
  .is-style-popup-menu .wp-block-buttons {
    *:nth-child(2) {
      position: relative;
      box-sizing: border-box;
      background-color: var(--wp--preset--color--accent-4);
      padding-top: 1.5em;
      margin-top: -0.5em;
      &::before {
        font-weight: 600;
        position: absolute;
        content: "All Other Provinces";
        font-size: 0.88rem;
        top: 0.625em;
        color: var(--wp--preset--color--contrast-3);
        left: 1.375rem;
      }
      html[lang*="fr"] &::before {
        content: "Autres provinces";
      }
    }
    *:nth-child(2),
    *:nth-child(3) {
      .wp-block-button__link {
        position: relative;
        padding-right: 0.5em;
        &::before {
          content: "";
          color: currentColor;
          display: inline-block;
          margin-right: 0.5em;
        }
      }
    }
    *:nth-child(3) {
      .wp-block-button__link {
        padding-top: 0.375rem;
      }
    }
    *:nth-child(2) {
      .wp-block-button__link {
        padding-bottom: 0.375rem;
      }
    }
  }
}
