@charset "UTF-8";
body * {
  box-sizing: border-box;
}

.fullwidth {
  width: 100% !important;
}

main.wp-block-group {
  margin-top: 0;
}

.wpforms-recaptcha-container {
  display: none;
}

strong {
  font-weight: 600;
}

:root .wp-block-group {
  --wp--preset--spacing--30: min(2.625rem, 4.5vw);
}

.wp-block-paragraph + .wp-block-paragraph {
  margin-top: 1.5rem;
}

.wp-block-post-content .display, .wp-block-post-content .display.wp-block-heading {
  font-size: 3.125rem !important;
  line-height: 1.13;
}
@media screen and (min-width: 600px) {
  .wp-block-post-content .display, .wp-block-post-content .display.wp-block-heading {
    font-size: 4.5rem !important;
  }
}
@media screen and (min-width: 1200px) {
  .wp-block-post-content .display, .wp-block-post-content .display.wp-block-heading {
    font-size: 6.1875rem !important;
  }
}

.wp-block-post-content :not(.single-post) h1, .wp-block-post-content :not(.single-post) h1.wp-block-heading {
  font-size: 2.25rem !important;
  line-height: 1.1;
}
@media screen and (min-width: 475px) {
  .wp-block-post-content :not(.single-post) h1, .wp-block-post-content :not(.single-post) h1.wp-block-heading {
    font-size: 2.5625rem !important;
  }
}
@media screen and (min-width: 782px) {
  .wp-block-post-content :not(.single-post) h1, .wp-block-post-content :not(.single-post) h1.wp-block-heading {
    font-size: 3.125rem !important;
    font-size: clamp(4vw, 3.875rem, 5.125vw) !important;
    letter-spacing: -0.015em;
  }
}
@media screen and (min-width: 1200px) {
  .wp-block-post-content :not(.single-post) h1, .wp-block-post-content :not(.single-post) h1.wp-block-heading {
    font-size: 4.0625rem !important;
    font-size: clamp(3w, 14.0625rem, 4.75vw) !important;
  }
}

.giant-heading,
h1.blog-title {
  font-size: 2.25rem !important;
  line-height: 1.13;
}
@media screen and (min-width: 475px) {
  .giant-heading,
  h1.blog-title {
    font-size: 2.5625rem !important;
  }
}
@media screen and (min-width: 782px) {
  .giant-heading,
  h1.blog-title {
    font-size: 3.125rem !important;
    font-size: clamp(4vw, 3.875rem, 5.125vw) !important;
  }
}
@media screen and (min-width: 1200px) {
  .giant-heading,
  h1.blog-title {
    font-size: 4.0625rem !important;
    font-size: clamp(3w, 14.0625rem, 4.75vw) !important;
  }
}

h1.blog-title {
  text-align: left;
}
@media screen and (min-width: 782px) {
  h1.blog-title {
    text-align: center;
  }
}

@media screen and (min-width: 600px) {
  .wp-block-post-content h2, .wp-block-post-content h2.wp-block-heading {
    font-size: 2.8125rem;
  }
}

.wp-block-post-content h3, .wp-block-post-content h3.wp-block-heading {
  font-size: 1.5625rem;
}
@media screen and (min-width: 600px) {
  .wp-block-post-content h3, .wp-block-post-content h3.wp-block-heading {
    font-size: 1.75rem;
  }
}
@media screen and (min-width: 1200px) {
  .wp-block-post-content h3, .wp-block-post-content h3.wp-block-heading {
    font-size: 2.125rem;
  }
}

.wp-block-post-content h4, .wp-block-post-content h4.wp-block-heading {
  font-size: 1.125rem;
  line-height: 1.25;
}
@media screen and (min-width: 600px) {
  .wp-block-post-content h4, .wp-block-post-content h4.wp-block-heading {
    font-size: 1.25rem;
  }
}

.subheading {
  font-size: 1.25rem !important;
}
@media screen and (min-width: 600px) {
  .subheading {
    font-size: 1.375rem !important;
  }
}

.wp-block-post-content p, .wp-block-post-content p.wp-block-heading,
.wp-block-post-content .paragraph,
.wp-block-post-content .paragraph.wp-block-heading {
  font-size: 0.875rem !important;
  line-height: 1.57;
}
@media screen and (min-width: 600px) {
  .wp-block-post-content p, .wp-block-post-content p.wp-block-heading,
  .wp-block-post-content .paragraph,
  .wp-block-post-content .paragraph.wp-block-heading {
    font-size: 1rem !important;
    line-height: 1.56;
  }
}

.wp-block-button__link.has-background a:hover {
  filter: brightness(90%);
}

@media screen and (max-width: 781px) {
  .wp-block-button__link {
    padding-left: 1.5em !important;
    padding-right: 1.5em !important;
  }
}

.big-green-equals {
  max-width: 53px;
}
@media screen and (min-width: 600px) {
  .big-green-equals {
    max-width: 64px;
  }
}
@media screen and (min-width: 768px) {
  .big-green-equals {
    max-width: none;
  }
}

.homepage-hero {
  height: calc(100vh - var(--nav-minheight));
}

:root {
  --icon-closex: url("data:image/svg+xml;base64,");
}

.wpforms-container {
  font-family: var(--wp--preset--font-family--metropolis);
}
.wpforms-container .wpforms-field-medium {
  max-width: 100% !important;
}
.wpforms-container .wpforms-field-medium::placeholder {
  color: #808080 !important;
}
.wpforms-container input,
.wpforms-container textarea,
.wpforms-container button {
  color: var(--wp--preset--color--contrast) !important;
  border-radius: 0.3125rem !important;
}
.wpforms-container button {
  padding-left: 1.5rem !important;
  padding-right: 1.5rem !important;
  padding-top: 0.7rem !important;
  padding-bottom: 0.6rem !important;
  height: auto !important;
  font-size: 0.92rem !important;
  transition: background-color 0.15s ease-in-out !important;
}
.wpforms-container button[type=submit] {
  transition: background-color 0.15s ease-in-out !important;
  color: white !important;
}
.wpforms-container em.wpforms-error {
  font-weight: 600 !important;
  font-size: 0.92rem !important;
}
.wpforms-container em.wpforms-error:before {
  top: 50% !important;
  transform: translate(0, calc(-2px - 50%));
}

footer .wpforms-container {
  display: block;
  margin-top: 0 !important;
  width: 100% !important;
}
footer .wpforms-container .wpforms-field-label {
  color: white !important;
}
footer .wpforms-container .wpforms-field-container {
  width: 100% !important;
}
footer .wpforms-container .wpforms-field-container .wpforms-field:first-child {
  padding-top: 0.5rem !important;
}
footer .wpforms-container .wpforms-submit-container {
  padding-top: 0 !important;
  margin-top: 0.125rem !important;
}
footer .wpforms-container .wpforms-recaptcha-container {
  padding: 0 !important;
  overflow: hidden !important;
}
footer .wpforms-container .wpforms-submit {
  transition: background-color 0.15s ease-in-out;
  background-color: var(--wp--preset--color--contrast-3) !important;
  color: white !important;
}
footer .wpforms-container .wpforms-submit:hover, footer .wpforms-container .wpforms-submit:active {
  transition: background-color 0.15s ease-in-out;
  background-color: var(--wp--preset--color--contrast-2) !important;
}

footer em.wpforms-error {
  padding: 0.5rem 1rem 0.375rem 1.625rem !important;
  background-color: white !important;
}
footer em.wpforms-error:before {
  top: 50% !important;
  transform: translate(4px, calc(-2px - 50%)) !important;
}

@media screen and (min-width: 600px) {
  .form-newsletter-blog .wpforms-form {
    display: flex !important;
    align-items: center;
    justify-content: stretch;
    height: 100%;
  }
  .form-newsletter-blog .wpforms-form > * {
    margin-top: 0;
    margin-bottom: 0;
  }
  .form-newsletter-blog .wpforms-form .wpforms-field {
    padding: 0 !important;
  }
  .form-newsletter-blog .wpforms-form input[type=email],
  .form-newsletter-blog .wpforms-form button[type=submit] {
    min-height: 48px;
  }
  .form-newsletter-blog .wpforms-form input[type=email] {
    border-top-right-radius: 0 !important;
    border-bottom-right-radius: 0 !important;
  }
  .form-newsletter-blog .wpforms-form .wpforms-container {
    display: block;
    width: 100% !important;
  }
  .form-newsletter-blog .wpforms-form .wpforms-field-container {
    flex-grow: 1;
    flex-basis: 100%;
  }
  .form-newsletter-blog .wpforms-form .wpforms-field-container,
  .form-newsletter-blog .wpforms-form .wpforms-submit-container {
    padding-top: 15px !important;
    margin: 0 !important;
  }
  .form-newsletter-blog .wpforms-form .wpforms-submit-container {
    position: relative;
  }
  .form-newsletter-blog .wpforms-form button[type=submit] {
    border-top-left-radius: 0 !important;
    border-bottom-left-radius: 0 !important;
    background-color: var(--wp--preset--color--contrast) !important;
    white-space: nowrap !important;
  }
}

input[type=email].wpforms-error,
input[type=text].wpforms-error,
input[type=tel].wpforms-error,
input[type=url].wpforms-error,
input[type=number].wpforms-error,
input[type=date].wpforms-error,
input[type=password].wpforms-error {
  color: black !important;
  background-color: #fae8e9 !important;
}

.submit-centred button[type=submit] {
  margin: 0 auto;
  display: block;
}
@media screen and (max-width: 475px) {
  .submit-centred button[type=submit] {
    width: 100%;
  }
}

@media screen and (max-width: 599px) {
  .wpforms-one-half {
    width: 100% !important;
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
}
@media screen and (max-width: 839px) {
  .wpforms-one-half:nth-last-child(2) {
    margin-bottom: 1rem;
  }
}

.wpforms-field-select select {
  min-width: 100% !important;
}

footer {
  position: relative;
}
footer #footer-decoration-container figure img {
  position: absolute;
  top: auto;
  bottom: 0;
  right: 0;
  display: block;
}
@media screen and (max-width: 767px) {
  footer #footer-decoration-desktop {
    display: none;
  }
}
@media screen and (min-width: 768px) {
  footer #footer-decoration-mobile {
    display: none;
  }
}
@media screen and (min-width: 782px) and (max-width: 1140px) {
  footer #footer-decorativespace {
    max-width: 0;
  }
}

.block-editor-iframe__body .editor-footer {
  background-color: var(--wp--preset--color--contrast);
}

footer {
  --deco-offset-width: 120px;
  --deco-offset: 15rem;
  --corner-radius: 2.5rem;
  background-color: var(--wp--preset--color--contrast);
  background-image: url(../assets/images/footer_decoration_mobile.svg);
  background-position: 100% bottom;
  background-repeat: no-repeat;
  background-size: var(--deco-offset-width);
  border-top-left-radius: var(--corner-radius);
  margin-top: calc(var(--corner-radius) * -1);
}
@media screen and (min-width: 768px) {
  footer {
    background-position: calc(100vw - var(--deco-offset) - var(--deco-offset-width)) bottom;
    --deco-offset-width: 300px;
    --deco-offset: 4rem;
    --corner-radius: 3.25rem;
    background-image: url(../assets/images/footer_decoration_desktop.svg);
  }
}
@media screen and (min-width: 900px) {
  footer {
    --corner-radius: 3rem;
    --deco-offset-width: 290px;
    --deco-offset: 2.5rem;
  }
}
@media screen and (min-width: 1320px) {
  footer {
    --deco-offset-width: 340px;
    --deco-offset: 3rem;
  }
}
@media screen and (min-width: 960px) and (max-width: 1140px) {
  #footer-address {
    transform: translateX(-3rem);
  }
}

@media screen and (min-width: 1140px) {
  #footer-menuitems {
    width: 820px !important;
  }
  #footer-menuitems .wp-block-group .wp-block-group {
    width: 360px;
  }
}
@media screen and (max-width: 767px) {
  #footer-menuitems button[type=submit] {
    width: 100% !important;
  }
}
@media screen and (min-width: 1400px) {
  #footer-menuitems {
    max-width: 20rem;
  }
}

@media screen and (max-width: 767px) {
  #footer-newsletter button[type=submit] {
    width: 100% !important;
  }
}

footer > .wp-block-group > .wp-block-columns {
  /* CAP MAX WIDTH OF FOOTER */
}
@media screen and (min-width: 1400px) {
  footer > .wp-block-group > .wp-block-columns {
    max-width: 1320px;
    margin-left: auto;
    margin-right: auto;
  }
}


:root {
  --wp--preset--color--tertiary: orange;
  --inner-margin: 1.5rem;
  --radius-tlbr-small: 0.625rem;
  --radius-tlbr-medium: 1rem;
  --radius-medium: 1.875rem;
  --radius-tlbr-large: 2.5rem 0 2.5rem 0;
  --radius-large: 3.75rem;
  --radius-tlbr-huge: 3.75rem 0 3.75rem 0;
  --font-light: "Metropolis Light", sans-serif;
  --font-medium: "Metropolis Medium", sans-serif;
}

.personnelcard--wrapper {
  cursor: pointer;
  width: 100%;
  margin-bottom: 0.75rem;
}
@media (min-width: 768px) {
  .personnelcard--wrapper {
    width: 202px;
  }
}
.personnelcard--wrapper + .personnelcard--wrapper {
  margin-left: var(--wp--custom--spacing--unit);
}

.personnelcard--link {
  text-decoration: none;
}

.personnelcard--unit {
  padding: var(--wp--custom--spacing--unit) 0 calc(var(--wp--custom--spacing--unit) * 0.5) 0;
  margin: 0 0 calc(var(--wp--custom--spacing--unit) * 0.5) 0;
  text-align: left;
  flex: 1 1 auto;
  width: 50%;
  justify-content: center;
  align-items: center;
}
@media (min-width: 375px) {
  .personnelcard--unit {
    width: 50%;
    width: 9.5rem;
  }
}
@media screen and (min-width: 680px) {
  .personnelcard--unit {
    width: 10.5rem;
    width: 11.25rem;
    flex-basis: 50%;
  }
}
@media (min-width: 768px) {
  .personnelcard--unit {
    min-width: 196px;
  }
}
@media (max-width: 479px) {
  .personnelcard--unit-wrapper {
    width: 100%;
  }
}

.personnelcard--titlearea,
.personnelcard--image {
  width: 100%;
}

.personnelcard--titlearea {
  box-sizing: border-box;
  display: flex;
  margin-top: 0.75rem;
  gap: 0.25rem;
  flex-direction: column;
  background-color: var(--cpa-green);
  border-radius: var(--radius-tlbr-small);
  padding: 0.75rem 0.5rem 0.625rem 0.5rem;
  min-height: 4.25rem !important;
  justify-content: center;
}
@media screen and (min-width: 768px) {
  .personnelcard--titlearea {
    min-height: 3rem !important;
  }
}

.personnelcard--image {
  display: block;
  width: 100%;
  height: 120px;
  position: relative;
  transition: all 0.15s ease-in-out;
  cursor: pointer;
  background-position: center;
  background-size: cover;
  border-radius: var(--radius-tlbr-medium);
}
@supports (aspect-ratio: 1/3) {
  .personnelcard--image {
    height: auto;
    aspect-ratio: 1/1;
  }
  @media (min-width: 375px) {
    .personnelcard--image {
      aspect-ratio: 4/3;
    }
  }
}
@media (min-width: 768px) {
  .personnelcard--image {
    width: 100%;
  }
}
.personnelcard--image:before, .personnelcard--image:after {
  position: absolute;
  width: 100%;
  left: 0;
}
.personnelcard--image:before {
  top: 0;
  height: 100%;
  content: "";
  transition: 0.3s all ease-in-out;
}
.personnelcard--image:hover {
  filter: grayscale(0%);
}
.personnelcard--image:hover:after {
  color: var(--wp--preset--color--foreground);
  opacity: 1;
  padding: calc(var(--wp--custom--spacing--unit) * 0.5);
}
.personnelcard--image:hover.empty-image {
  background-color: var(--wp--preset--color--foreground);
}

.personnelcard--image.empty-image,
.personnelpopup--image.empty-image {
  background: url('data:image/svg+xml;utf-8,<svg xmlns="http://www.w3.org/2000/svg" width="100" height="100" viewBox="0 0 100 100"><circle cx="50" cy="42.287" r="8.257" style="fill:none;stroke:%232f1360;stroke-linecap:round;stroke-miterlimit:10;stroke-width:2px"/><path d="M65.541,66.085a15.528,15.528,0,0,0-8.5-13.844" style="fill:none;stroke:%232f1360;stroke-linecap:round;stroke-miterlimit:10;stroke-width:2px"/><path d="M42.963,52.241a15.528,15.528,0,0,0-8.5,13.844" style="fill:none;stroke:%232f1360;stroke-linecap:round;stroke-miterlimit:10;stroke-width:2px"/><rect width="100" height="100" style="fill:none"/></svg>') no-repeat;
  background-size: contain;
  background-position: center;
  background-color: var(--wp--preset--color--tertiary);
}

.personnelpopup--image.empty-image {
  background-color: white;
}

.personnelcard--name {
  margin: 0;
  margin-top: 0 !important;
  color: white !important;
  text-align: center;
  line-height: 1;
}
@media screen and (min-width: 475px) {
  .personnelcard--name {
    font-size: 1.125rem !important;
  }
}

@media (min-width: 375px) {
  .personnelpopup--nametitle {
    padding-left: var(--margin-smmd);
  }
}
@media (min-width: 768px) {
  .personnelpopup--nametitle {
    padding-left: 0;
  }
}

.personnelcard--role {
  margin: 0 !important;
  font-size: 0.8125rem;
  line-height: 1.2 !important;
  color: white;
  text-align: center;
}
@media screen and (max-width: 640px) {
  .personnelcard--role {
    font-size: 0.88rem;
  }
}

#personnelcard--module.personnelcard--module-wrapper {
  margin: 0;
  padding: 0;
  display: block;
  width: 100%;
}

.personnelcard--blocks-wrapper {
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
}
@media (min-width: 600px) {
  .personnelcard--blocks-wrapper {
    flex-direction: row;
  }
}
.personnelcard--links-wrapper {
  display: block;
  padding: 1rem;
}
@media (min-width: 600px) {
  .personnelcard--links-wrapper {
    flex-basis: 200px;
  }
}
@media (min-width: 768px) {
  .personnelcard--links-wrapper {
    text-align: right;
    padding: 1rem var(--inner-margin) 1rem 0;
    flex-basis: 240px;
  }
}
.personnelcard--links-container {
  display: block;
  transform: translateY(-1.75em);
}
.personnelcard--links-list {
  list-style-type: none;
  padding: 0;
  position: relative;
}
.personnelcard--links-list li + li {
  margin-top: 0.5rem;
}
.personnelcard--links-list a {
  display: inline-block;
  line-height: var(--lineheight-tiny);
  font-size: var(--fontsize-medium);
  color: black;
  font-weight: 400;
  text-decoration: none;
  padding: calc(var(--wp--custom--spacing--unit) * 0.5) 0;
}
.personnelcard--links-list a.active {
  color: var(--wp--preset--color--foreground);
}
.personnelcard--cards-wrapper {
  width: 100%;
  display: block;
  flex-direction: row;
  padding-left: var(--inner-margin);
  border-left: 2px solid var(--wp--preset--color--tertiary);
}
.personnelcard--cards-container {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  max-height: 0;
  overflow: hidden;
  height: auto;
  transition: 0.75s all ease-in-out;
}
.personnelcard--cards-container > * {
  opacity: 0;
}
.personnelcard--cards-container.active {
  max-height: 1000px;
  transition-delay: 0.375s;
}
.personnelcard--cards-container.active > * {
  opacity: 1;
}

.personnelpopup--wrapper {
  margin: 0;
  position: fixed;
  z-index: 1001;
  width: 100vw;
  height: 100vh;
  max-width: none;
  top: 0;
  left: 0;
  background-color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  transition-delay: 0;
  transition: opacity 0.5s ease-in-out, backdrop-filter 0.375s ease-in-out, margin-top 0.375s ease-in;
  background-color: rgba(var(--wp--preset--color--foreground), 0.35);
  margin-top: 1rem;
  opacity: 0;
}
.personnelpopup--wrapper.active {
  opacity: 1;
  backdrop-filter: blur(4px);
  margin-top: 0;
}
.personnelpopup--container {
  box-sizing: border-box;
  position: relative;
  background: white !important;
  color: var(--wp--preset--color--background);
  width: 500px;
  padding: var(--inner-margin);
  box-shadow: 0 0 2rem rgba(0, 0, 0, 0.45);
  transition: 0.5s all ease-in-out;
  margin-top: -0.5rem;
  overflow: auto;
  border-radius: var(--radius-medium);
  border: 2px solid var(--cpa-green);
  display: flex;
  flex-direction: column;
  max-width: calc(100vw - 30px);
}
@media (min-width: 768px) {
  .personnelpopup--container {
    width: 500px;
  }
}
@media (max-width: 639px) {
  .personnelpopup--container {
    max-height: calc(100vh - var(--navheight) - var(--margin-mobile) * 2);
    top: var(--navheight);
  }
}
@media (min-width: 768px) {
  .personnelpopup--container {
    width: 660px;
    display: flex;
    flex-direction: row;
    flex: 0 1 auto;
  }
}
.fadein > .personnelpopup--container, .fadeout > .personnelpopup--container {
  margin-top: 0;
}
.personnelpopup--header {
  border-bottom: 2px solid var(--cpa-green);
  margin-bottom: 1.5rem;
}
.personnelpopup--header > *:last-child {
  margin-bottom: 0.75rem;
}
@media (min-width: 768px) {
  .personnelpopup--header > *:last-child {
    padding-bottom: 0.75rem;
