  background-size: calc(var(--buttonsize) * 0.4);
  background-color: transparent;
  border: 2px solid white;
}
.pause-preview:active {
  opacity: 0.4;
}
.pause-preview:hover {
  opacity: 1;
}
@media (prefers-reduced-transparency) {
  .pause-preview {
    opacity: 1;
  }
}
.pause-preview:focus {
  outline: 2px dotted #003254;
  outline-offset: 2px;
}