body {
  // border: 20px solid blue;
  * {
    box-sizing: border-box;
  }
}
.fullwidth {
  width: 100% !important;
}

main.wp-block-group {
  margin-top: 0;
}
.wpforms-recaptcha-container {
  display: none;
}
strong {
  font-weight: 600;
}

:root {
  .wp-block-group {
    --wp--preset--spacing--30: min(2.625rem, 4.5vw);
  }
}

.wp-block-paragraph + .wp-block-paragraph {
  margin-top: 1.5rem;
}
